<script>
  import {Line} from 'vue-chartjs'

  export default {
    name: "LineChart",
    extends: Line,
    props: ['labels', 'label', 'data', 'options'],
    mounted() {
      // 
      this.gradient = this.$refs.canvas.getContext('2d').createLinearGradient(0, 0, 0, 450);
      this.gradient.addColorStop(0, 'rgba(54, 163 ,188, 0.5)');
      this.gradient.addColorStop(0.5, 'rgba(54, 163, 188, 0.25)');
      this.gradient.addColorStop(1, 'rgba(54, 163, 188, 0)');

      this.renderChart({
        labels: this.labels,
        datasets: [
          {
            label: this.label,
            data: this.data,
            // borderColor: '#36a3bc',
            borderWidth: 1,
            backgroundColor: this.gradient, //
            pointBackgroundColor: '#36a3bc', //x Y
            pointBorderColor: 'white', //x Yborder
            fontSize: 12, //
            radius: '2' //
          }
        ]
      }, {
        responsive: true, //100%
        maintainAspectRatio: false, //
        // events: ["mousemove", "mouseout", "touchstart", "touchmove", "touchend"], //click
        scales: {
          yAxes: [{ //Y
            gridLines: { //Y
              // display: false, //false
            },
            ticks: {
              beginAtZero: true,
              userCallback: function (label, index, labels) {
                // when the floored value is the same as the value we have a whole number
                if (labels[0] > 1000 && Math.floor(label) === label) {
                  return label / 1000 + 'k';
                } else {
                  return label
                }
              }
            }
          }],
          xAxes: [{ //X
            gridLines: { //X
              display: false, //false
            }
          }]
        }
      })
    }
  }
</script>



